'use client'

import Image from 'next/image'
import photos from '@/data/photos.json'
import type { Photo } from '@/types'
import SectionHeader from '@/components/ui/SectionHeader'

const photosData = photos as Photo[]

// Function to determine grid classes based on size
const getGridClasses = (size: Photo['size'], index: number) => {
  if (size === 'large') {
    return 'col-span-2 row-span-2'
  }
  if (size === 'medium') {
    return index % 3 === 0 ? 'col-span-2 row-span-1' : 'col-span-1 row-span-2'
  }
  return 'col-span-1 row-span-1'
}

export default function BentoPhotoGrid() {
  // Use first 9 photos for the grid
  const gridPhotos = photosData.slice(0, 9)

  return (
    <section className="py-16 md:py-24 bg-primary-light/30">
      <div className="max-w-7xl mx-auto px-6">
        <SectionHeader
          title="Life at Alpha Zeta"
          subtitle="A glimpse into our brotherhood, events, and memories"
        />

        <div className="mt-16 grid grid-cols-2 md:grid-cols-4 auto-rows-[200px] gap-4">
          {gridPhotos.map((photo, index) => (
            <div
              key={photo.id}
              className={`group relative overflow-hidden rounded-2xl cursor-pointer ${getGridClasses(photo.size, index)}`}
            >
              <Image
                src={photo.src}
                alt={photo.caption}
                fill
                className="object-cover transition-transform duration-500 group-hover:scale-110"
                unoptimized
              />

              {/* Overlay */}
              <div className="absolute inset-0 bg-gradient-to-t from-accent/80 via-accent/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />

              {/* Caption */}
              <div className="absolute bottom-0 left-0 right-0 p-4 translate-y-full group-hover:translate-y-0 transition-transform duration-300">
                <p className="text-white font-medium text-sm">{photo.caption}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
