'use client'

import { useEffect, useRef, useState } from 'react'
import Image from 'next/image'
import companies from '@/data/companies.json'
import type { Company } from '@/types'

const companyData: Company[] = companies

export default function CompanyCarousel() {
  const scrollRef = useRef<HTMLDivElement>(null)
  const [isPaused, setIsPaused] = useState(false)

  useEffect(() => {
    const scrollContainer = scrollRef.current
    if (!scrollContainer) return

    let animationId: number
    let scrollPosition = 0
    const scrollSpeed = 0.5

    const scroll = () => {
      if (!isPaused && scrollContainer) {
        scrollPosition += scrollSpeed

        // Reset position when we've scrolled halfway (since we duplicate the content)
        if (scrollPosition >= scrollContainer.scrollWidth / 2) {
          scrollPosition = 0
        }

        scrollContainer.scrollLeft = scrollPosition
      }
      animationId = requestAnimationFrame(scroll)
    }

    animationId = requestAnimationFrame(scroll)

    return () => cancelAnimationFrame(animationId)
  }, [isPaused])

  // Duplicate companies for infinite scroll effect
  const duplicatedCompanies = [...companyData, ...companyData]

  return (
    <section className="py-16 md:py-24 border-y border-white/5">
      <div className="max-w-7xl mx-auto px-6 mb-8">
        <p className="text-center text-secondary/50 text-sm uppercase tracking-widest">
          Where Our Members Work
        </p>
      </div>

      <div
        ref={scrollRef}
        className="overflow-hidden cursor-pointer"
        onMouseEnter={() => setIsPaused(true)}
        onMouseLeave={() => setIsPaused(false)}
      >
        <div className="flex gap-12 items-center">
          {duplicatedCompanies.map((company, index) => (
            <div
              key={`${company.id}-${index}`}
              className="flex-shrink-0 w-40 h-16 flex items-center justify-center opacity-50 hover:opacity-100 grayscale hover:grayscale-0 transition-all duration-300"
            >
              <Image
                src={company.logo}
                alt={company.name}
                width={160}
                height={64}
                className="max-w-full max-h-full object-contain"
                unoptimized
              />
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
